<script lang="ts">
  import type { Message } from "../types";

  export let content: string;
  export let type: "user" | "bot";
  export let timestamp: Date;
  export let context: NonNullable<Message["context"]> | null = null;

  const formatTime = (date: Date) => {
    return date.toLocaleTimeString([], { hour: "2-digit", minute: "2-digit" });
  };
</script>

<div
  class="message-container {type === 'user' ? 'user-message' : 'bot-message'}"
>
  <div
    class="message-content {type === 'user' ? 'user-content' : 'bot-content'}"
  >
    <div class="avatar">
      {#if type === "user"}
        <span>U</span>
      {:else}
        <span>B</span>
      {/if}
    </div>
    <div class="bubble-container">
      <div
        class="message-bubble {type === 'user' ? 'user-bubble' : 'bot-bubble'}"
      >
        {#if type === "user"}
          <div class="message-text">{content}</div>
        {:else}
          <div class="markdown-content">
            {@html content}
          </div>
        {/if}
      </div>
      {#if context && context.length > 0}
        <div class="context-links">
          <div class="context-label">Sources:</div>
          {#each context as source}
            <a
              href={source.website_url}
              target="_blank"
              rel="noopener noreferrer"
              class="source-link"
            >
              <svg
                class="link-icon"
                viewBox="0 0 24 24"
                fill="none"
                stroke="currentColor"
              >
