import MarkdownIt from 'markdown-it'
import hljs from 'highlight.js/lib/core'
// Import only the languages we need
import javascript from 'highlight.js/lib/languages/javascript'
import typescript from 'highlight.js/lib/languages/typescript'
import python from 'highlight.js/lib/languages/python'
import bash from 'highlight.js/lib/languages/bash'
import json from 'highlight.js/lib/languages/json'
import xml from 'highlight.js/lib/languages/xml' // For HTML
import css from 'highlight.js/lib/languages/css'
import markdown from 'highlight.js/lib/languages/markdown'
import php from 'highlight.js/lib/languages/php'

// Register the languages
hljs.registerLanguage('javascript', javascript)
hljs.registerLanguage('typescript', typescript)
hljs.registerLanguage('python', python)
hljs.registerLanguage('bash', bash)
hljs.registerLanguage('json', json)
hljs.registerLanguage('xml', xml)
hljs.registerLanguage('html', xml)
hljs.registerLanguage('css', css)
hljs.registerLanguage('markdown', markdown)
hljs.registerLanguage('php', php)

interface MarkdownItToken {
  type: string
  tag: string
  attrs: [string, string][] | null
  map: [number, number] | null
  nesting: number
  level: number
  children: MarkdownItToken[] | null
  content: string
  markup: string
  info: string
  meta: unknown
  block: boolean
  hidden: boolean
}

// Create markdown-it instance with options
const md = new MarkdownIt({
  html: true,
  linkify: true,
  typographer: true,
  highlight: function (str: string, lang: string): string {
    if (lang && hljs.getLanguage(lang)) {
      try {
        return hljs.highlight(str, { language: lang }).value
      } catch {
        // Fall back to default if language not found
      }
    }
    return '' // Return empty string for unknown languages
  }
})

// Override fence rendering to add custom classes
md.renderer.rules.fence = function (
  tokens: MarkdownItToken[],
  idx: number,
  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  options: unknown,
  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  env: unknown,
  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  self: unknown
): string {
  const token = tokens[idx]
  return `<pre><code class="hljs language-${token.info.trim()}">${token.content}</code></pre>`
}

// Override inline code rendering to add custom class
md.renderer.rules.code_inline = function (
  tokens: MarkdownItToken[],
  idx: number,
  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  options: unknown,
  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  env: unknown,
  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  self: unknown
): string {
  const token = tokens[idx]
  return `<code class="inline-code">${token.content}</code>`
}

export function formatMarkdown(text: string): string {
  return md.render(text)
}

export function formatInlineMarkdown(text: string): string {
  return md.renderInline(text)
}

export default md
