<script lang="ts">
  import { onMount, onDestroy } from "svelte";
  import {
    chatStore,
    addProduct,
    removeProduct,
    setDocType,
    resetFilters,
    setProducts,
  } from "../stores/chat";
  import { PRODUCTS, DOC_TYPES } from "../types";

  let isProductDropdownOpen = false;
  let isDocTypeDropdownOpen = false;
  let filterRef: HTMLDivElement;
  let docTypeFilterRef: HTMLDivElement;
  let isProductsLocked = false;

  // Handle click outside
  function handleClickOutside(event: MouseEvent) {
    const target = event.target as HTMLElement;

    // Check if click is outside the dropdowns
    if (filterRef && !filterRef.contains(target)) {
      isProductDropdownOpen = false;
    }
    if (docTypeFilterRef && !docTypeFilterRef.contains(target)) {
      isDocTypeDropdownOpen = false;
    }
  }

  // Handle widget click outside
  function handleWidgetClickOutside() {
    isProductDropdownOpen = false;
    isDocTypeDropdownOpen = false;
  }

  // Listen for doc-names-changed event
  onMount(() => {
    const widget = document.querySelector("fluent-ai-chatbot");
    if (widget) {
      const docNamesHandler = (event: CustomEvent) => {
        const docNames = event.detail.docNames;
        if (Array.isArray(docNames) && docNames.length > 0) {
          isProductsLocked = true;
          setProducts(docNames);
        }
      };

      // Check for initial doc-names attribute
      const docNamesAttr = widget.getAttribute("doc-names");
      if (docNamesAttr) {
        try {
          const docNames = JSON.parse(docNamesAttr);
          if (Array.isArray(docNames) && docNames.length > 0) {
            isProductsLocked = true;
            setProducts(docNames);
          }
        } catch (e) {
          console.error("Invalid doc-names format:", e);
        }
      }

      widget.addEventListener(
        "doc-names-changed",
        docNamesHandler as EventListener
      );

      // Listen for clicks on the document
      document.addEventListener("click", handleClickOutside, true);

      // Listen for widget click outside event
      widget.addEventListener("click-outside", handleWidgetClickOutside);

      // Cleanup
      return () => {
        widget.removeEventListener(
          "doc-names-changed",
          docNamesHandler as EventListener
        );
        widget.removeEventListener("click-outside", handleWidgetClickOutside);
        document.removeEventListener("click", handleClickOutside, true);
      };
    }
  });

  // Get selected doc type label
  $: selectedDocTypeLabel =
    DOC_TYPES.find((type) => type.value === $chatStore.selectedDocType)
      ?.label || "Select Doc Type";
</script>

<div class="filter-container">
  <div class="filter-wrapper">
    <!-- Doc Type Filter -->
    <div class="dropdown-container" bind:this={docTypeFilterRef}>
      <button
        class="filter-button"
        on:click|stopPropagation={() =>
          (isDocTypeDropdownOpen = !isDocTypeDropdownOpen)}
      >
        <span>{selectedDocTypeLabel}</span>
        <svg
          class="dropdown-icon {isDocTypeDropdownOpen ? 'rotate' : ''}"
          fill="none"
          stroke="currentColor"
          viewBox="0 0 24 24"
        >
          <path
            stroke-linecap="round"
            stroke-linejoin="round"
            stroke-width="2"
            d="M19 9l-7 7-7-7"
          />
        </svg>
      </button>

      {#if isDocTypeDropdownOpen}
        <div
          class="dropdown-menu left"
          role="listbox"
          aria-label="Document type options"
          tabindex="0"
          on:click|stopPropagation
          on:keydown|stopPropagation
        >
          <div class="dropdown-content">
            {#each DOC_TYPES as type}
              <button
                class="dropdown-item {$chatStore.selectedDocType === type.value
                  ? 'selected'
                  : ''}"
                role="option"
                aria-selected={$chatStore.selectedDocType === type.value}
                on:click|stopPropagation={() => {
                  setDocType(type.value);
                  isDocTypeDropdownOpen = false;
                }}
                on:keydown|stopPropagation={(e) => {
                  if (e.key === "Enter" || e.key === " ") {
                    e.preventDefault();
                    setDocType(type.value);
                    isDocTypeDropdownOpen = false;
                  }
                }}
              >
                {type.label}
                {#if $chatStore.selectedDocType === type.value}
                  <svg
                    class="check-icon"
                    fill="none"
                    stroke="currentColor"
                    viewBox="0 0 24 24"
                  >
                    <path
                      stroke-linecap="round"
                      stroke-linejoin="round"
                      stroke-width="2"
                      d="M5 13l4 4L19 7"
                    />
                  </svg>
                {/if}
              </button>
            {/each}
          </div>
        </div>
      {/if}
    </div>

    <!-- Product Filter -->
    {#if !isProductsLocked}
      <div class="dropdown-container" bind:this={filterRef}>
        <button
          class="filter-button"
          aria-expanded={isProductDropdownOpen}
          aria-haspopup="true"
          on:click|stopPropagation={() => {
            isProductDropdownOpen = !isProductDropdownOpen;
          }}
        >
          <span>Select Products</span>
          <svg
            class="dropdown-icon {isProductDropdownOpen ? 'rotate' : ''}"
            fill="none"
            stroke="currentColor"
            viewBox="0 0 24 24"
            aria-hidden="true"
          >
            <path
              stroke-linecap="round"
              stroke-linejoin="round"
              stroke-width="2"
              d="M19 9l-7 7-7-7"
            />
          </svg>
        </button>

        {#if isProductDropdownOpen}
          <div
            class="dropdown-menu right"
            role="listbox"
            aria-label="Product selection options"
            tabindex="0"
            on:click|stopPropagation
            on:keydown|stopPropagation
          >
            <div class="dropdown-content product-list">
              {#each PRODUCTS as product}
                <button
                  class="checkbox-item {$chatStore.selectedProducts.includes(
                    product.value
                  )
                    ? 'selected'
                    : ''}"
                  role="option"
                  aria-selected={$chatStore.selectedProducts.includes(
                    product.value
                  )}
                  on:click|stopPropagation={() => {
                    if ($chatStore.selectedProducts.includes(product.value)) {
                      removeProduct(product.value);
                    } else {
                      addProduct(product.value);
                    }
                  }}
                  on:keydown|stopPropagation={(e) => {
                    if (e.key === "Enter" || e.key === " ") {
                      e.preventDefault();
                      if ($chatStore.selectedProducts.includes(product.value)) {
                        removeProduct(product.value);
                      } else {
                        addProduct(product.value);
                      }
                    }
                  }}
                >
                  <div class="checkbox-label">
                    <input
                      type="checkbox"
                      checked={$chatStore.selectedProducts.includes(
                        product.value
                      )}
                      class="checkbox"
                      aria-label={product.label}
                      readonly
                    />
                    <span class="checkbox-text">{product.label}</span>
                  </div>
                </button>
              {/each}
            </div>
          </div>
        {/if}
      </div>
    {/if}
  </div>

  <!-- Selected Products Tags -->
  {#if $chatStore.selectedProducts.length > 0}
    <div class="tag-container" role="list">
      {#each $chatStore.selectedProducts as value}
        {@const product = PRODUCTS.find((p) => p.value === value)}
        {#if product}
          <span class="tag" role="listitem">
            {product.label}
            {#if !isProductsLocked}
              <button
                on:click|stopPropagation={() => removeProduct(value)}
                class="tag-remove"
                aria-label="Remove {product.label}"
              >
                <svg
                  class="tag-icon"
                  fill="none"
                  stroke="currentColor"
                  viewBox="0 0 24 24"
                  aria-hidden="true"
                >
                  <path
                    stroke-linecap="round"
                    stroke-linejoin="round"
                    stroke-width="2"
                    d="M6 18L18 6M6 6l12 12"
                  />
                </svg>
              </button>
            {/if}
          </span>
        {/if}
      {/each}
    </div>
  {/if}
</div>

