import { writable, derived, get } from 'svelte/store';
import type { DocType, Message } from '../types';
import type { Chat as ChatType } from '../types';
import api from '../utils/api';
import { formatMarkdown } from '../utils/markdown';

interface Chat {
    id: string;
    user_id: string;
    title: string;
    created_at: string;
}

export interface ChatStore {
    messages: Message[];
    isLoading: boolean;
    chats: Chat[];
    currentChatId: string | null;
    showHistory: boolean;
    selectedProducts: string[];
    selectedDocType: DocType;
    error: string | null;
}

const initialState: ChatStore = {
    messages: [],
    isLoading: false,
    chats: [],
    currentChatId: null,
    showHistory: false,
    selectedProducts: [],
    selectedDocType: 'user-docs',
    error: null
};

export const chatStore = writable<ChatStore>(initialState);

// Derived stores for computed values
export const currentChat = derived(chatStore, ($store) =>
    $store.chats.find(chat => chat.id === $store.currentChatId)
);

export const formattedMessages = derived(chatStore, ($store) =>
    $store.messages.map(msg => ({
        ...msg,
        message: msg.role === 'ai' ? formatMarkdown(msg.message) : msg.message
    }))
);

export const filteredChats = derived(chatStore, ($store) => {
    return [...$store.chats].sort((a, b) =>
        new Date(b.created_at).getTime() - new Date(a.created_at).getTime()
    );
});

// Helper function to update store with loading and error states
const withAsync = async (
    action: () => Promise<void>,
    errorMessage: string
) => {
    chatStore.update(s => ({ ...s, isLoading: true, error: null }));
    try {
        await action();
    } catch (err) {
        console.error(errorMessage, err);
        chatStore.update(s => ({ ...s, error: errorMessage }));
    } finally {
        chatStore.update(s => ({ ...s, isLoading: false }));
    }
};

export const fetchChats = async (userId: string) => {
    await withAsync(async () => {
        const { data } = await api.get(`/chats/${userId}`);
        if (data.chats) {
            chatStore.update(s => ({ ...s, chats: data.chats }));
        }
    }, 'Failed to load chats');
};

export const fetchMessages = async (chatId: string) => {
    await withAsync(async () => {
        const { data } = await api.get(`/chat/${chatId}`);
        if (data.messages) {
            chatStore.update(s => ({
                ...s,
                messages: data.messages.map((msg: Message) => ({
                    ...msg,
                    message: msg.role === 'ai' ? formatMarkdown(msg.message) : msg.message
                }))
            }));
        }
    }, 'Failed to load messages');
};

export const selectChat = async (chat: ChatType | null) => {
    chatStore.update(s => ({ ...s, currentChatId: chat?.id || null }));
    if (!chat) {
        clearMessages();
    } else {
        await fetchMessages(chat.id);
    }
};

export const sendMessage = async (content: string) => {
    if (!content.trim()) return;

    const store = get(chatStore);
    if (store.isLoading) return;

    await withAsync(async () => {
        const chatId = store.currentChatId || '';

        // Add user message immediately for optimistic UI
        const userMessage: Message = {
            id: Date.now().toString(),
            chat_id: chatId,
            role: 'human',
            message: content,
            context: null,
            created_at: new Date().toISOString()
        };

        // Add streaming message placeholder
        const streamingMessage: Message = {
            id: 'streaming',
            chat_id: chatId,
            role: 'ai',
            message: '',
            context: null,
            created_at: new Date().toISOString()
        };

        chatStore.update(s => ({
            ...s,
            messages: [...s.messages, userMessage, streamingMessage]
        }));

        const userId = localStorage.getItem('userId');
        const body = {
            message: content,
            doc_names: store.selectedProducts,
            doc_types: store.selectedDocType === 'all' ? ['user-docs', 'developer-docs'] : [store.selectedDocType],
            user_id: userId,
            chat_id: chatId,
        };

        let previousText = '';
        let accumulatedText = '';

        const response = await api.post('/chat', body, {
            responseType: 'text',
            headers: {
                Accept: 'text/event-stream',
            },
            onDownloadProgress: (progressEvent) => {
                const text = progressEvent.event?.target?.responseText || '';

                if (!text || text === previousText) return;

                // Get only the new content
                const newContent = text.slice(previousText.length);
                previousText = text;

                // Split and process only the new content
                const lines = newContent.split('\n');

                for (const line of lines) {
                    if (!line.trim() || !line.startsWith('data:')) continue;
                    const data = line.replace('data:', '').trim();
                    if (!data) continue;

                    try {
                        const parsedData = JSON.parse(data);
                        chatStore.update(s => {
                            const streamingMsg = s.messages.find(m => m.id === 'streaming');
                            if (!streamingMsg) return s;

                            // Handle context event
                            if (Array.isArray(parsedData)) {
                                return {
                                    ...s,
                                    messages: s.messages.map(m =>
                                        m.id === 'streaming'
                                            ? {
                                                ...m,
                                                context: parsedData.map(ctx => ({
                                                    id: ctx.id,
                                                    title: ctx.title,
                                                    website_url: ctx.website_url,
                                                }))
                                            }
                                            : m
                                    )
                                };
                            }

                            // Handle AI response event
                            if (typeof parsedData === 'string') {
                                accumulatedText += parsedData;
                                return {
                                    ...s,
                                    messages: s.messages.map(m =>
                                        m.id === 'streaming'
                                            ? {
                                                ...m,
                                                message: formatMarkdown(accumulatedText)
                                            }
                                            : m
                                    )
                                };
                            }

                            // Handle chat ID event
                            if ('chat_id' in parsedData) {
                                if (!s.currentChatId) {
                                    fetchChats(userId!);
                                }
                                return s;
                            }

                            // Handle done event
                            if ('done' in parsedData && parsedData.done) {
                                const finalMessage: Message = {
                                    id: Date.now().toString(),
                                    chat_id: chatId,
                                    role: 'ai',
                                    message: formatMarkdown(accumulatedText),
                                    context: streamingMsg.context,
                                    created_at: new Date().toISOString()
                                };
                                return {
                                    ...s,
                                    messages: s.messages.map(m =>
                                        m.id === 'streaming'
                                            ? finalMessage
                                            : m
                                    )
                                };
                            }

                            // Handle error event
                            if ('error' in parsedData) {
                                throw new Error(parsedData.error);
                            }

                            return s;
                        });
                    } catch (e) {
                        console.error('Error parsing SSE data:', e);
                        if (e instanceof Error && e.message !== 'Error parsing SSE data:') {
                            throw e;
                        }
                    }
                }
            },
        });

        if (response.status !== 200) {
            throw new Error('Failed to send message');
        }
    }, 'Failed to send message. Please try again.');
};

export const clearMessages = () => {
    chatStore.update(state => ({
        ...state,
        messages: [],
        currentChatId: null
    }));
};

export const toggleHistory = () => {
    chatStore.update(state => ({
        ...state,
        showHistory: !state.showHistory
    }));
};

export const deleteChat = async (chatId: string) => {
    await withAsync(async () => {
        const response = await api.delete(`/chat/${chatId}`);

        if (response.data.success) {
            chatStore.update(state => ({
                ...state,
                chats: state.chats.filter(c => c.id !== chatId),
                messages: state.currentChatId === chatId ? [] : state.messages,
                currentChatId: state.currentChatId === chatId ? null : state.currentChatId
            }));
        } else {
            throw new Error('Failed to delete chat');
        }
    }, 'Failed to delete chat');
};

export const updateFilter = (searchTerm: string = '', dateRange: 'all' | 'today' | 'week' | 'month' = 'all') => {
    chatStore.update(state => ({
        ...state,
        filter: { searchTerm, dateRange }
    }));
};

export const addProduct = (product: string) => {
    chatStore.update(state => {
        if (!state.selectedProducts.includes(product)) {
            return {
                ...state,
                selectedProducts: [...state.selectedProducts, product]
            };
        }
        return state;
    });
};

export const removeProduct = (product: string) => {
    chatStore.update(state => ({
        ...state,
        selectedProducts: state.selectedProducts.filter(p => p !== product)
    }));
};

export const setProducts = (products: string[]) => {
    chatStore.update(state => ({
        ...state,
        selectedProducts: products
    }));
};

export const setDocType = (docType: DocType) => {
    chatStore.update(state => ({
        ...state,
        selectedDocType: docType
    }));
};

export const resetFilters = () => {
    chatStore.update(state => ({
        ...state,
        selectedProducts: [],
        selectedDocType: 'user-docs'
    }));
}; 