<script lang="ts">
  import { onMount } from "svelte";
  import {
    chatStore,
    toggleHistory,
    clearMessages,
    resetFilters,
  } from "./lib/stores/chat";
  import { userStore } from "./lib/stores/user";
  import ChatHistory from "./lib/components/ChatHistory.svelte";
  import ChatView from "./lib/components/ChatView.svelte";

  let showHistory = $derived($chatStore.showHistory);

  onMount(() => {
    // Initialize user ID
    userStore.initializeUser();

    // Add initial bot message locally
    chatStore.update((store) => ({
      ...store,
      messages: [
        {
          id: Date.now().toString(),
          chat_id: "",
          role: "ai",
          message:
            "Hi, I'm Fluent ChatBot assistant, ask me anything about Fluent products",
          formattedMessage:
            "Hi, I'm Fluent ChatBot assistant, ask me anything about Fluent products",
          context: null,
          created_at: new Date().toISOString(),
        },
      ],
    }));
  });
</script>

<main>
  <!-- Header -->
  <header class={showHistory ? "history-header" : "main-header"}>
    {#if showHistory}
      <div class="title">History</div>
      <button
        class="icon-button"
        aria-label="Close history"
        onclick={toggleHistory}
      >
        <svg
          xmlns="http://www.w3.org/2000/svg"
          class="icon"
          viewBox="0 0 20 20"
          fill="currentColor"
        >
          <path
            fill-rule="evenodd"
            d="M4.293 4.293a1 1 0 011.414 0L10 8.586l4.293-4.293a1 1 0 111.414 1.414L11.414 10l4.293 4.293a1 1 0 01-1.414 1.414L10 11.414l-4.293 4.293a1 1 0 01-1.414-1.414L8.586 10 4.293 5.707a1 1 0 010-1.414z"
            clip-rule="evenodd"
          />
        </svg>
      </button>
    {:else}
      <div class="title">Fluent ChatBot</div>
      <div class="button-group">
        <button
          class="icon-button"
          aria-label="Chat history"
          onclick={toggleHistory}
        >
          <svg
            xmlns="http://www.w3.org/2000/svg"
            class="icon"
            viewBox="0 0 20 20"
            fill="currentColor"
          >
            <path
              fill-rule="evenodd"
              d="M10 18a8 8 0 100-16 8 8 0 000 16zm1-12a1 1 0 10-2 0v4a1 1 0 00.293.707l2.828 2.829a1 1 0 101.415-1.415L11 9.586V6z"
              clip-rule="evenodd"
            />
          </svg>
        </button>
        <button
          class="icon-button"
          aria-label="Refresh chat"
          onclick={() => {
            clearMessages();
            resetFilters();
            chatStore.update((store) => ({
              ...store,
              messages: [
                {
                  id: Date.now().toString(),
                  chat_id: "",
                  role: "ai",
                  message:
                    "Hi, I'm Fluent ChatBot assistant, ask me anything about Fluent products",
                  formattedMessage:
                    "Hi, I'm Fluent ChatBot assistant, ask me anything about Fluent products",
                  context: null,
                  created_at: new Date().toISOString(),
                },
              ],
            }));
          }}
        >
          <svg
            xmlns="http://www.w3.org/2000/svg"
            class="icon"
            viewBox="0 0 20 20"
            fill="currentColor"
          >
            <path
              fill-rule="evenodd"
              d="M4 2a1 1 0 011 1v2.101a7.002 7.002 0 0111.601 2.566 1 1 0 11-1.885.666A5.002 5.002 0 005.999 7H9a1 1 0 010 2H4a1 1 0 01-1-1V3a1 1 0 011-1zm.008 9.057a1 1 0 011.276.61A5.002 5.002 0 0014.001 13H11a1 1 0 110-2h5a1 1 0 011 1v4a1 1 0 11-2 0v-2.101a7.002 7.002 0 01-11.601-2.566 1 1 0 01.61-1.276z"
              clip-rule="evenodd"
            />
          </svg>
        </button>
      </div>
    {/if}
  </header>

  <div class="content">
    {#if showHistory}
      <ChatHistory />
    {:else}
      <ChatView />
    {/if}
  </div>
</main>

