import { mount } from 'svelte';
import App from './App.svelte';
import appCss from './assets/app.css?inline';
import markdownCss from './assets/markdown.css?inline';

class FluentAIChatbot extends HTMLElement {
    private clickHandler: (event: MouseEvent) => void;
    private isOpen: boolean = false;

    static get observedAttributes() {
        return ['doc-names'];
    }

    constructor() {
        super();
        this.attachShadow({ mode: 'open' });
        this.clickHandler = this.handleClickOutside.bind(this);
    }

    attributeChangedCallback(name: string, oldValue: string, newValue: string) {
        if (name === 'doc-names' && newValue !== oldValue) {
            try {
                const docNames = JSON.parse(newValue);
                if (Array.isArray(docNames)) {
                    // Dispatch custom event to notify the Svelte component
                    this.dispatchEvent(new CustomEvent('doc-names-changed', {
                        detail: { docNames },
                        composed: true,
                        bubbles: true
                    }));
                }
            } catch (e) {
                console.error('Invalid doc-names format:', e);
            }
        }
    }

    private handleClickOutside(event: MouseEvent) {
        const target = event.target as HTMLElement;
        const path = event.composedPath();

        // Check if click is outside the shadow DOM
        const isOutside = !path.some(element =>
            element === this ||
            (element instanceof HTMLElement && element.closest('fluent-ai-chatbot') === this)
        );

        if (isOutside && this.isOpen) {
            this.toggleWidget();
            // Dispatch click-outside event
            this.dispatchEvent(new CustomEvent('click-outside', {
                composed: true,
                bubbles: true
            }));
        }
    }

    private toggleWidget() {
        this.isOpen = !this.isOpen;
        const container = this.shadowRoot?.querySelector('#fluent-ai-chatbot') as HTMLElement;
        if (container) {
            if (this.isOpen) {
                container.style.display = 'flex';
                // Trigger animation after display is set
                setTimeout(() => {
                    container.style.opacity = '1';
                    container.style.transform = 'translateY(0) scale(1)';
                }, 0);
            } else {
                container.style.opacity = '0';
                container.style.transform = 'translateY(20px) scale(0.95)';
                // Hide after animation
                setTimeout(() => {
                    container.style.display = 'none';
                }, 300);
            }
        }
        const button = this.shadowRoot?.querySelector('#chat-button') as HTMLElement;
        if (button) {
            button.classList.toggle('active', this.isOpen);
        }
    }

    connectedCallback() {
        // Handle initial doc-names attribute if present
        const docNamesAttr = this.getAttribute('doc-names');
        if (docNamesAttr) {
            try {
                const docNames = JSON.parse(docNamesAttr);
                if (Array.isArray(docNames)) {
                    this.dispatchEvent(new CustomEvent('doc-names-changed', {
                        detail: { docNames },
                        composed: true,
                        bubbles: true
                    }));
                }
            } catch (e) {
                console.error('Invalid doc-names format:', e);
            }
        }

        if (this.shadowRoot) {
            // Create a style element for the widget positioning and app styles
            const style = document.createElement('style');
            style.textContent = `
                @import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600;700&display=swap');
                
                :host {
                    position: fixed;
                    bottom: 20px;
                    right: 20px;
                    z-index: 9999;
                    display: flex;
                    flex-direction: column;
                    align-items: flex-end;
                }

                #chat-button {
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    width: 3.5rem;
                    height: 3.5rem;
                    padding: 0;
                    background-color: #2563eb;
                    color: white;
                    border: none;
                    border-radius: 50%;
                    cursor: pointer;
                    box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
                    transition: all 0.2s ease;
                    z-index: 10000;
                }

                #chat-button:hover {
                    background-color: #1d4ed8;
                    transform: translateY(-1px);
                }

                #chat-button.active {
                    background-color: #1e40af;
                }

                #chat-button .chat-icon {
                    width: 1.5rem;
                    height: 1.5rem;
                    transition: transform 0.3s ease;
                    display: block;
                }

                #chat-button .close-icon {
                    width: 1.5rem;
                    height: 1.5rem;
                    display: none;
                }

                #chat-button.active .chat-icon {
                    display: none;
                }

                #chat-button.active .close-icon {
                    display: block;
                }

                #fluent-ai-chatbot {
                    width: 400px;
                    height: 600px;
                    max-height: calc(100vh - 100px);
                    overflow: visible;
                    display: none;
                    flex-direction: column;
                    position: relative;
                    background: white;
                    border-radius: 1rem;
                    box-shadow: 
                        rgba(0, 0, 0, 0.05) 0px 6px 24px 0px,
                        rgba(0, 0, 0, 0.08) 0px 0px 0px 1px;
                    margin-bottom: 1rem;
                    opacity: 0;
                    transform: translateY(20px) scale(0.95);
                    transform-origin: bottom right;
                    transition: opacity 0.3s ease, transform 0.3s cubic-bezier(0.34, 1.56, 0.64, 1);
                    overflow: hidden;
                }

                /* Ensure header has rounded top corners */
                :host ::slotted(header) {
                    border-top-left-radius: 1rem;
                    border-top-right-radius: 1rem;
                }

                /* Ensure the filter container has proper styling */
                :host ::slotted(.filter-container) {
                    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
                    background: white;
                }

                /* Add subtle border to the widget */
                #fluent-ai-chatbot::before {
                    content: '';
                    position: absolute;
                    inset: 0;
                    border-radius: 1rem;
                    pointer-events: none;
                    border: 1px solid rgba(0, 0, 0, 0.1);
                }

                /* Ensure dropdowns are visible */
                :host ::slotted(*) {
                    position: relative;
                }

                /* Ensure dropdowns are visible in shadow DOM */
                :host ::slotted(.dropdown-menu) {
                    position: absolute;
                    z-index: 99999;
                }

                ${appCss}
                ${markdownCss}
            `;
            this.shadowRoot.appendChild(style);

            // Create chat button first
            const button = document.createElement('button');
            button.id = 'chat-button';
            button.innerHTML = `
                <svg class="chat-icon" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" 
                        d="M8 12h.01M12 12h.01M16 12h.01M21 12c0 4.418-4.03 8-9 8a9.863 9.863 0 01-4.255-.949L3 20l1.395-3.72C3.512 15.042 3 13.574 3 12c0-4.418 4.03-8 9-8s9 3.582 9 8z" />
                </svg>
                <svg class="close-icon" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" 
                        d="M6 18L18 6M6 6l12 12" />
                </svg>
            `;
            button.addEventListener('click', () => this.toggleWidget());

            // Create a container for the app
            const container = document.createElement('div');
            container.id = 'fluent-ai-chatbot';

            // Add elements in correct order (container first, then button)
            this.shadowRoot.appendChild(container);
            this.shadowRoot.appendChild(button);

            // Mount the Svelte app
            mount(App, {
                target: container,
            });

            // Add click event listener to handle clicks outside the widget
            document.addEventListener('click', this.clickHandler, true);
        }
    }

    disconnectedCallback() {
        // Clean up event listener
        document.removeEventListener('click', this.clickHandler, true);
    }
}

// Register the custom element
customElements.define('fluent-ai-chatbot', FluentAIChatbot);

// Auto-inject the widget when the script loads
const injectWidget = () => {
    // Check if widget already exists
    if (!document.querySelector('fluent-ai-chatbot')) {
        const widget = document.createElement('fluent-ai-chatbot');
        document.body.appendChild(widget);
    }
};

// Inject immediately if document is ready
if (document.readyState === 'complete' || document.readyState === 'interactive') {
    injectWidget();
} else {
    // Otherwise wait for DOM to be ready
    document.addEventListener('DOMContentLoaded', injectWidget);
} 