import type { AxiosError, AxiosResponse } from 'axios'
import { config } from './config'

// Create no-op functions for production
const noOp = () => { };

// Create conditional logger that only logs in development
export const logger = {
  logRequest: config.enableLogging
    ? (method: string, url: string, data?: unknown, params?: unknown) => {
      console.group('API Request')
      console.log('Method:', method)
      console.log('URL:', url)
      if (data) console.log('Body:', data)
      if (params) console.log('Params:', params)
      console.groupEnd()
    }
    : noOp,

  logResponse: config.enableLogging
    ? (response: AxiosResponse) => {
      console.group('API Response')
      console.log('Status:', response.status)
      console.log('Data:', response.data)
      console.groupEnd()
    }
    : noOp,

  logError: config.enableLogging
    ? (error: AxiosError) => {
      console.group('API Error')
      console.error('Message:', error.message)
      if (error.response) {
        console.error('Status:', error.response.status)
        console.error('Data:', error.response.data)
      }
      console.groupEnd()
    }
    : noOp,
}
