<script lang="ts">
  import { chatStore, sendMessage } from "../stores/chat";
  import Message from "./Message.svelte";
  import ProductFilter from "./ProductFilter.svelte";
  import { onMount } from "svelte";

  let messageInput = $state("");
  let chatContainer: HTMLDivElement;

  let messages = $derived($chatStore.messages);
  let isLoading = $derived($chatStore.isLoading);

  const scrollToBottom = () => {
    if (chatContainer) {
      chatContainer.scrollTop = chatContainer.scrollHeight;
    }
  };

  // Scroll to bottom when messages change
  $effect(() => {
    if (messages) {
      setTimeout(scrollToBottom, 100); // Small delay to ensure content is rendered
    }
  });

  // Scroll to bottom on initial load
  onMount(() => {
    scrollToBottom();
  });

  // Scroll to bottom after each update
  $effect(() => {
    scrollToBottom();
  });

  const handleSubmit = async () => {
    if (!messageInput.trim()) return;

    const content = messageInput;
    messageInput = "";

    await sendMessage(content);
  };
</script>

<div class="chat-container">
  <!-- Product Filter -->
  <div class="filter-container">
    <ProductFilter />
  </div>

  <!-- Chat Container -->
  <div bind:this={chatContainer} class="messages-container">
    {#each messages as message (message.id)}
      {#if message.id === "streaming"}
        <div class="message-wrapper">
          <div class="message-content">
            <div class="avatar bot-avatar">
              <span>B</span>
            </div>
            <div class="message-bubble">
              {#if !message.message}
                <div class="thinking">
                  <span>Thinking</span>
                  <span class="dots">
                    <span class="dot dot-1">.</span>
                    <span class="dot dot-2">.</span>
                    <span class="dot dot-3">.</span>
                  </span>
                </div>
              {:else}
                <div class="prose typing-animation">
                  {@html message.message}
                </div>
              {/if}
            </div>
          </div>
        </div>
      {:else}
        <Message
          content={message.message}
          type={message.role === "human" ? "user" : "bot"}
          timestamp={new Date(message.created_at)}
          context={message.context}
        />
      {/if}
    {/each}
  </div>

  <!-- Input Area -->
  <form
    onsubmit={(e) => {
      e.preventDefault();
      handleSubmit();
    }}
    class="input-container"
  >
    <input
      type="text"
      bind:value={messageInput}
      placeholder="Type your message..."
      class="message-input"
      onkeydown={(e) => {
        if (e.key === "Enter" && !e.shiftKey) {
          e.preventDefault();
          handleSubmit();
        }
      }}
      disabled={isLoading}
    />
  </form>
</div>

<style>
  .chat-container {
    height: 100%;
    display: flex;
    flex-direction: column;
  }

  .filter-container {
    flex-shrink: 0;
  }

  .messages-container {
    flex: 1;
    overflow-y: auto;
    padding: 1rem;
    background-color: #f9fafb; /* bg-gray-50 */
    display: flex;
    flex-direction: column;
    gap: 1rem;
  }

  .message-wrapper {
    display: flex;
    justify-content: flex-start;
    width: 100%;
    padding-right: 1rem; /* Add padding to prevent text from touching edge */
  }

  .message-content {
    display: flex;
    gap: 1rem;
    max-width: 90%; /* Reduce from 95% to 90% to add more margin */
  }

  .avatar {
    width: 2.5rem;
    height: 2.5rem;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
    font-weight: 600;
    flex-shrink: 0;
  }

  .bot-avatar {
    background-color: #10b981; /* bg-emerald-500 */
  }

  .message-bubble {
    background-color: white;
    border: 1px solid #e5e7eb; /* border-gray-200 */
    border-radius: 0.5rem; /* rounded-lg */
    padding: 1rem;
    box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.05); /* shadow-sm */
    word-wrap: break-word; /* Ensure text breaks properly */
    overflow-wrap: break-word; /* Modern browsers */
    width: 100%; /* Ensure bubble takes full width of its container */
    max-width: 100%; /* Limit width to prevent overflow */
  }

  .thinking {
    display: flex;
    align-items: center;
    gap: 0.5rem;
    color: #111827; /* text-gray-900 */
  }

  .dots {
    display: flex;
    gap: 0.25rem;
  }

  .dot {
    animation: bounce 1s infinite;
  }

  .dot-1 {
    animation-delay: 0ms;
  }

  .dot-2 {
    animation-delay: 150ms;
  }

  .dot-3 {
    animation-delay: 300ms;
  }

  @keyframes bounce {
    0%,
    100% {
      transform: translateY(0);
    }
    50% {
      transform: translateY(-5px);
    }
  }

  .prose {
    font-size: 0.875rem; /* prose-sm */
    max-width: 100%; /* Ensure content doesn't overflow */
    overflow-wrap: break-word;
  }

  .input-container {
    flex-shrink: 0;
    border-top: 1px solid #e5e7eb; /* border-gray-200 */
    background-color: white;
    padding: 1rem;
    width: 100%;
    box-sizing: border-box;
  }

  .message-input {
    width: 100%;
    padding: 0.5rem 1rem;
    border: 1px solid #e5e7eb; /* border-gray-200 */
    border-radius: 0.5rem; /* rounded-lg */
    box-sizing: border-box;
    max-width: 100%;
  }

  .message-input:focus {
    outline: none;
    border-color: #3b82f6; /* border-blue-500 */
  }

  /* Typing animation */
  @keyframes typing {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }

  .typing-animation {
    animation: typing 0.5s ease-in-out;
  }
</style>
